<template lang="html">
  <section v-if="userInfoReady" class="user-info">
    <el-form :model="formData" :rules="rules" label-width="100px" ref="loginForm">
      <el-form-item label="">
        <el-input v-model="formData.username" disabled></el-input>
      </el-form-item>

      <el-form-item label="" prop="nickname">
        <el-input v-model="formData.nickname" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="" prop="sex">
        <el-select v-model="formData.sex" placeholder="">
          <el-option label="" :value="1"></el-option>
          <el-option label="" :value="0"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('loginForm')"></el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
export default {
  data () {
    return {
      formData: {
        username: '',
        nickname: '',
        sex: ''
      },
      rules: {
        nickname: [
          { required: true, message: '' },
          { max: 20, message: '20' }
        ],
        sex: [
          { required: true, message: '' }
        ]
      },
      userInfoReady: false
    }
  },
  created () {
    this.fetchUserInfo()
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios.put('/api-u/users/me', this.formData).then(response => {
            this.$message.success('')
            this.$router.push('/home')
          }).catch(error => {
            console.log(error)
            this.$message.error('')
          })
        } else {
          this.$message.warning('')
        }
      })
    },
    fetchUserInfo () {
      this.$axios.get('/api-u/users/current')
        .then(response => {
          const { id, username, nickname, sex } = response.data
          Object.assign(this.formData, { id, username, nickname, sex })

          this.userInfoReady = true
        })
        .catch(error => {
          console.log(error)
        })
    }
  }
}
</script>
